import React, { Component } from "react";
import Typical from "react-typical";
import Switch from "react-switch";

class Header extends Component {
  titles = [];

  constructor() {
    super();
    this.state = { checked: false };
    this.onThemeSwitchChange = this.onThemeSwitchChange.bind(this);
    this.applyPickedLanguage = this.applyPickedLanguage.bind(this);
  }

  onThemeSwitchChange(checked) {
    this.setState({ checked });
    this.setTheme();
  }

  setTheme() {
    const body = document.body;
    const newTheme = body.getAttribute("data-theme") === "dark" ? "light" : "dark";
    body.setAttribute("data-theme", newTheme);
  }

  applyPickedLanguage(language) {
    this.props.loadResumeFromPath(language);
    this.setState({ language });
  }

  render() {
    if (this.props.sharedData) {
      const { name } = this.props.sharedData;
      this.titles = this.props.sharedData.titles.map((x) => [x.toUpperCase(), 1500]).flat();
    }

    const HeaderTitleTypeAnimation = React.memo(() => {
      return (
        <div
          style={{
            fontSize: "2.7em",
            color: "#fff",
            lineHeight: "1.2em", // Fixed line height to prevent vertical shifting
          }}
        >
          <Typical steps={this.titles} loop={50} wrapper="p" />
        </div>
      );
    });

    return (
      <header
        id="home"
        style={{
          height: "100vh", // Cover the full viewport height
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          position: "relative",
        }}
      >
        {/* Background image with filter */}
        <div
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
            backgroundImage: "url(/images/FMY.JPG)",
            backgroundSize: "cover",
            backgroundPosition: "center left",
            filter: "brightness(10%)",
            zIndex: 0,
          }}
        ></div>

        {/* Content overlay */}
        <div
          className="text-content"
          style={{
            zIndex: 1,
            padding: "0 5%",
            color: "#fff",
            textAlign: "center",
          }}
        >
          <h1
            style={{
              fontSize: "6.5em",
              fontWeight: "bold",
              lineHeight: "1.2em",
              margin: "0 0 20px 0",
              color: "#fff",
            }}
          >
            {this.props.sharedData.name}
          </h1>

          {/* Only the animated title text */}
          <HeaderTitleTypeAnimation />

          {/* Language Toggle */}
          <div
            className="language-toggle"
            style={{
              display: "flex",
              gap: "20px",
              alignItems: "center",
              justifyContent: "center",
              marginTop: "20px",
            }}
          >
            <span
              onClick={() => this.applyPickedLanguage("en")}
              className="iconify language-icon"
              data-icon="twemoji:flag-for-flag-united-states"
              data-inline="false"
              style={{
                cursor: "pointer",
                fontSize: "3.5em", // Increased size
                filter: this.props.language === "en" ? "brightness(50%)" : "none",
              }}
            ></span>
            <span
              onClick={() => this.applyPickedLanguage("tr")}
              className="iconify language-icon"
              data-icon="twemoji:flag-for-flag-turkey"
              data-inline="false"
              style={{
                cursor: "pointer",
                fontSize: "3.5em", // Increased size
                filter: this.props.language === "tr" ? "brightness(50%)" : "none",
              }}
            ></span>
          </div>

          {/* Theme Switcher */}
          <div style={{ display: "flex", justifyContent: "center", marginTop: "15px" }}>
            <Switch
              checked={this.state.checked}
              onChange={this.onThemeSwitchChange}
              offColor="#baaa80"
              onColor="#353535"
              className="react-switch"
              width={120}
              height={50}
              uncheckedIcon={
                <span
                  className="iconify"
                  data-icon="twemoji:wolf"
                  data-inline="false"
                  style={{
                    display: "block",
                    height: "100%",
                    fontSize: 30,
                    textAlign: "end",
                    marginLeft: "20px",
                    color: "#353239",
                  }}
                ></span>
              }
              checkedIcon={
                <span
                  className="iconify"
                  data-icon="noto-v1:sun-with-face"
                  data-inline="false"
                  style={{
                    display: "block",
                    height: "100%",
                    fontSize: 30,
                    textAlign: "end",
                    marginLeft: "10px",
                    color: "#353239",
                  }}
                ></span>
              }
            />
          </div>
        </div>
      </header>
    );
  }
}

export default Header;